@import url('https://fonts.googleapis.com/css?family=Raleway&display=swap');
* {
  font-family: 'Raleway', sans-serif;
}
body,
html,
#root {
  background-color: #563d7c;
}

.interface-container {
  width: 100%;
}

.title {
  position: relative;
  width: auto;
  display: table;
  color: white;
  padding: 5px 0px;
  border-bottom: 2px solid white;
  margin-bottom: 15px;
}
.warning {
  position: absolute;
  left: 50%;
  top: 50%;
  text-align: center;
  color: white;
  transform: translate(-50%, -50%);
}
.navbar-brand .fab {
  font-size: 50px;
  margin-right: 10px;
  color: #563d7c;
}
.navbar-brand {
  display: flex !important;
  align-items: center;
}
.mylink{
  width:35px;
  height:35px;
  display:flex !important;
  align-items:center;
  justify-content:center;
  border-radius:50px;
}
.mylink:hover{
  color:black;
  background-color:#f8f9fa;
}
.rrt-title{
  font-size:12px!important;
  margin-bottom: 0 !important;
}
 .close-toastr {
   font-size:18px!important
 }

 .toastr{
   min-height:20px !important;
   height:auto !important;
 }
@keyframes inwidth {
  0% {
    width: 0;
    left: -2px;
  }
  50% {
    width: 60px;
    left: -62px;
  }
  100% {
    width: 0;
    left: -2px;
  }
}
